%;\n  white-space: nowrap;\n  display: flex;\n  position: relative;\n\n  & > * {\n    box-sizing: border-box;\n  }\n}\n\n/**\n * The outline of where the element will go if dropped, displayed while dragging\n */\n.rowLandingPad {\n  border: none;\n  box-shadow: none;\n  outline: none;\n\n  * {\n    opacity: 0 !important;\n  }\n\n  &::before {\n    background-color: lightblue;\n    border: 2px dotted black;\n    content: '';\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: -1;\n  }\n}\n\n/**\n * Alternate appearance of the landing pad when the dragged location is invalid\n */\n.rowCancelPad {\n  @extend .rowLandingPad;\n\n  &::before {\n    background-color: #e6a8ad;\n  }\n}\n\n/**\n * Nodes matching the search conditions are highlighted\n */\n.rowSearchMatch {\n  box-shadow: inset 0 -7px 7px -3px #0080ff;\n}\n\n/**\n * The node that matches the search conditions and is currently focused\n */\n.rowSearchFocus {\n  box-shadow: inset 0 -7px 7px -3px #fc6421;\n}\n\n%rowItem {\n  display: inline-block;\n  vertical-align: middle;\n}\n\n.rowContents {\n  @extend %rowItem;\n  position: relative;\n  height: 100%;\n  flex: 1 0 auto;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.rowLabel {\n  @extend %rowItem;\n  flex: 0 1 auto;\n  padding-right: 20px;\n}\n\n.rowToolbar {\n  @extend %rowItem;\n  flex: 0 1 auto;\n  display: flex;\n}\n\n.toolbarButton {\n  @extend %rowItem;\n}\n\n.collapseButton,\n.expandButton {\n  appearance: none;\n  border: none;\n  background: transparent;\n  padding: 0;\n  z-index: 2;\n  position: absolute;\n  top: 45%;\n  width: 30px;\n  height: 30px;\n  transform: translate3d(-50%, -50%, 0);\n  cursor: pointer;\n\n  &::after {\n    content: '';\n    position: absolute;\n    transform-origin: 7px 4px;\n    transform: translate3d(-50%, -20%, 0);\n    border: solid transparent 10px;\n    border-left-width: 7px;\n    border-right-width: 7px;\n    border-top-color: gray;\n  }\n\n  &:hover::after {\n    border-top-color: black;\n  }\n\n  &:focus {\n    outline: none;\n\n    &::after {\n      filter: drop-shadow(0 0 1px #83bef9) drop-shadow(0 0 1px #83bef9)\n        drop-shadow(0 0 1px #83bef9);\n    }\n  }\n}\n\n.expandButton::after {\n  transform: translate3d(-50%, -20%, 0) rotateZ(-90deg);\n}\n\n/**\n * Line for under a node with children\n */\n.lineChildren {\n  height: 100%;\n  display: inline-block;\n}\n\n/* ==========================================================================\n   Scaffold\n\n    Line-overlaid blocks used for showing the tree structure\n   ========================================================================== */\n.lineBlock {\n  height: 100%;\n  position: relative;\n  display: inline-block;\n  flex: 0 0 auto;\n}\n\n.absoluteLineBlock {\n  @extend .lineBlock;\n  position: absolute;\n  top: 0;\n}\n\n/* Highlight line for pointing to dragged row destination\n   ========================================================================== */\n$highlight-color: #36c2f6;\n$highlight-line-size: 6px; // Make it an even number for clean rendering\n\n/**\n * +--+--+\n * |  |  |\n * |  |  |\n * |  |  |\n * +--+--+\n */\n.highlightLineVertical {\n  z-index: 3;\n\n  &::before {\n    position: absolute;\n    content: '';\n    background-color: $highlight-color;\n    width: $highlight-line-size;\n    margin-left: $highlight-line-size / -2;\n    left: 50%;\n    top: 0;\n    height: 100%;\n  }\n\n  @keyframes arrow-pulse {\n    $base-multiplier: 10;\n    0% {\n      transform: translate(0, 0);\n      opacity: 0;\n    }\n    30% {\n      transform: translate(0, 30% * $base-multiplier);\n      opacity: 1;\n    }\n    70% {\n      transform: translate(0, 70% * $base-multiplier);\n      opacity: 1;\n    }\n    100% {\n      transform: translate(0, 100% * $base-multiplier);\n      opacity: 0;\n    }\n  }\n\n  &::after {\n    content: '';\n    position: absolute;\n    height: 0;\n    margin-left: -1 * $highlight-line-size / 2;\n    left: 50%;\n    top: 0;\n    border-left: $highlight-line-size / 2 solid transparent;\n    border-right: $highlight-line-size / 2 solid transparent;\n    border-top: $highlight-line-size / 2 solid white;\n    animation: arrow-pulse 1s infinite linear both;\n  }\n}\n\n/**\n * +-----+\n * |     |\n * |  +--+\n * |  |  |\n * +--+--+\n */\n.highlightTopLeftCorner {\n  &::before {\n    z-index: 3;\n    content: '';\n    position: absolute;\n    border-top: solid $highlight-line-size $highlight-color;\n    border-left: solid $highlight-line-size $highlight-color;\n    box-sizing: border-box;\n    height: calc(50% + #{$highlight-line-size / 2});\n    top: 50%;\n    margin-top: $highlight-line-size / -2;\n    right: 0;\n    width: calc(50% + #{$highlight-line-size / 2});\n  }\n}\n\n/**\n * +--+--+\n * |  |  |\n * |  |  |\n * |  +->|\n * +-----+\n */\n.highlightBottomLeftCorner {\n  $arrow-size: 7px;\n  z-index: 3;\n\n  &::before {\n    content: '';\n    position: absolute;\n    border-bottom: solid $highlight-line-size $highlight-color;\n    border-left: solid $highlight-line-size $highlight-color;\n    box-sizing: border-box;\n    height: calc(100% + #{$highlight-line-size / 2});\n    top: 0;\n    right: $arrow-size;\n    width: calc(50% - #{$arrow-size - ($highlight-line-size / 2)});\n  }\n\n  &::after {\n    content: '';\n    position: absolute;\n    height: 0;\n    right: 0;\n    top: 100%;\n    margin-top: -1 * $arrow-size;\n    border-top: $arrow-size solid transparent;\n    border-bottom: $arrow-size solid transparent;\n    border-left: $arrow-size solid $highlight-color;\n  }\n}\n"],"sourceRoot":""}]);
